*{margin:0;padding:0;box-sizing:border-box}body{margin:0;font-family:"Open Sans",-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;background:#111;color:#fff;font-size:100%;line-height:1.5;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}a{color:#ccc;text-decoration:none;transition:all 250ms ease-in-out}a:hover{color:#fff}a.active{color:#47d794}.section{margin-bottom:2rem}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.Nav{position:fixed;top:0;width:100%;height:3rem;display:flex;align-items:center}.Nav__list{width:100%;list-style:none;display:flex;margin:0;padding:0}.Nav__item{margin:0 1rem}.Nav__item:first-child{margin-left:2rem}.Nav__item--right{margin-left:auto}.Nav__link{padding:.25rem .5rem}.Nav__link--sign:first-child{margin-left:0}.Nav__link--sign:last-child{margin-right:0}.Nav__link--alt{border:2px solid #ccc;border-radius:.5rem}.Nav__link--alt.active{border-color:#47d794;background-color:#47d794;color:#fff}

.Main{margin:3rem 2rem 0 2rem}

.Home{padding-top:2rem;font-family:"Comfortaa",-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;font-weight:700;-webkit-animation:reveal 2s ease-in-out;animation:reveal 2s ease-in-out;color:#eee}.Home__title{color:#47d794;font-size:3rem;-webkit-animation:highlight 3s ease-in-out;animation:highlight 3s ease-in-out}.Home__previews{display:grid;grid-template-columns:repeat(auto-fit, minmax(363px, auto));grid-gap:2rem}.Home__logo{margin-bottom:4rem}.Preview{display:flex;flex-direction:column;align-items:center;justify-content:flex-start;border-radius:1rem;opacity:0.9;transition:all 250ms ease-in-out;padding:4rem 0.25rem 4rem 0.25rem;transform:translateY(0)}.Preview__title{font-size:2rem}.Preview:hover{opacity:1;transform:translateY(-0.5rem)}.Preview.red{background:#d85353}.Preview.blue{background:#5959d6}.Preview.green{background:#4a984a}.Preview.cyan{background:#5cc5c5}.Preview.purple{background:#c655c6}.Preview.yellow{background:#d2d26a}@-webkit-keyframes reveal{from{opacity:0;font-weight:300}to{opacity:1;font-weight:700}}@keyframes reveal{from{opacity:0;font-weight:300}to{opacity:1;font-weight:700}}@-webkit-keyframes highlight{from{color:#fff}to{color:#47d794}}@keyframes highlight{from{color:#fff}to{color:#47d794}}

